@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@800&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Merienda&display=swap');

.main {
  background-image: url(images/jr-korpa-9XngoIpxcEo-unsplash.jpg);
  background-position: center;
  background-size: cover;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.glass {
  background-color: rgb(0, 0, 0, 0.5);
  width: 70vw;
  height: 80vh;
  backdrop-filter: blur(0.4rem);
  border-top-right-radius: 0.9rem;
  border-top-left-radius: 0.9rem;
  border: solid 1px #0d0314;
  border-top: none;
  box-shadow: 0 0 5px rgb(37, 37, 37);
  z-index: 2;
  display: flex;
}
/* Content */
.content {
  display: flex;
  flex-direction: column;
  width: 50vw;
  justify-content: flex-start;
  align-items: center;
}

#form {
  padding: 1.2rem;
}
.search {
  background-color: rgb(0, 0, 0, 0.5);
  border: none;
  padding: 0.8rem;
  font-size: 1rem;
  font-family: "Montserrat", sans-serif;
  color: white;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  border-radius: 1.2rem;
  box-shadow: 0 0 10px rgb(0, 0, 0, 0.2);
  transition: ease all 0.3s;
}
#search-box {
  width: 35vw;
}
#search-box:focus {
  outline: 0.5px solid rgba(255, 255, 255, 0.767);
  background-color: rgb(0, 0, 0, 0.8);
}
#search-btn:hover {
  transform: scale(1.1);
}
.responsive{
  display: none;
}

/* changing-content */
.base {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
#city {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}
.home {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 45vh;
}
.upper {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}
#temp {
  font-size: 7rem;
  font-weight: bold;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.404);
}
span {
  font-size: 5rem;
}
#date {
  font-size: 1.2rem;
}
.description {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40vw;
}
#desc {
  font-size: 2rem;
  margin: 2rem;
}

.about {
  display: grid;
  grid-template-columns: 33% 33% 33%;
  height: 55vh;
  width: 40vw;
  overflow-y: scroll;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
  border-radius: 1rem;
}
.about::-webkit-scrollbar {
  display: none;
}

.block {
  padding: 1rem;
  margin: 0.6rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: center;
  border-radius: 1rem;
  background-color: rgb(0, 0, 0, 0.5);
  transition: ease transform 0.3s;
  box-shadow: 0 0 10px rgb(0, 0, 0, 0.2);
}
.block:hover {
  transform: scale(1.07);
}
.forecastDate {
  margin-bottom: 0.3rem;
}

.more {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.box {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 43vw;
  height: 45vh;
  margin: 1rem;
  background-color: rgb(0, 0, 0, 0.5);
  border-radius: 1rem;
}

.moreData {
  display: flex;
  justify-content: space-around;
  width: 80%;
  height: 100%;
  /* margin-left: 1.5rem; */
}
.col {
  display: grid;
  grid-template-columns: 33% 33% 33%;
  height: 100%;
  width: 100%;
  justify-content: space-around;
}
p {
  color: rgba(255, 255, 255, 0.932);
  margin-bottom: 0.3rem;
  font-size: 0.7rem;
}
span {
  font-size: 0.6rem;
}
.aqi {
  transform: scale(1.1);
  margin: 1rem;
}
.aqi div {
  font-size: 1.4rem;
}

.tracker {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 1rem;
  justify-content: space-around;
  align-items: center;
  height: 15%;
}
.time {
  display: flex;
  align-items: baseline;
  justify-content: space-around;
  width: 100%;
}
.times {
  display: flex;
  align-items: baseline;
}
.time p {
  margin-right: 0.3rem;
}
.level {
  background-color: white;
  width: 50%;
  height: 0.13rem;
  margin: 0.7rem;
}
.completed {
  background-color: yellow;
  /* width: 100%; */
  height: 100%;
}
.sun {
  background-color: yellow;
  /* border: solid 1px yellow; */
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  position: relative;
  bottom: 0.55rem;
  /* right: 0.55rem; */
  /* left: 100%; */
}
@media only screen and (max-width: 1000px) {
  #search-btn{
    display: none;
  }
}
@media only screen and (max-width: 800px) {
  .glass {
  width: 80vw;
  height: 80vh;
  }
  .content{
    width: 100vw;
  }
  .base{
    justify-content: space-around;
  }
  #form{
    padding: 1rem;
  }
  #search-box {
    width: 65vw;
  }
  .description{
    margin-top: 0rem;
  }
  .responsive{
    margin-top: 1rem;
    display: flex;
  }
  .about {
    display: grid;
    grid-template-columns: 50% 50%;
    width: 70vw;
  }
  .glass{
    overflow: hidden;
  }
  .box {
    width: 70vw;
    height: 43vh;
    background-color: rgb(0, 0, 0, 0.5);
    border-radius: 1rem;
  }
  .col {
    display: grid;
    grid-template-columns: 50% 50%;
  }
  .moreData{
    overflow-y: scroll;
  }
  .moreData::-webkit-scrollbar {
    display: none;
  }
}
@media only screen and (max-width: 600px) {
  .description{
    flex-direction: column;
    margin-top: 0rem;
    width: 70vw;
    padding: 0rem;
  }
  #desc{
    font-size: 1.8rem;
    text-align: center;
  }
  #temp{
    font-size: 6rem;
  }
  #city{
    margin: 0.5rem;
  }
}