nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  background-color: rgb(0, 0, 0, 0.3);
  border-bottom-left-radius: 0.9rem;
  border-top-left-radius: 0.9rem;
  width: 20vw;
}
.nav-below{
    display: none;
}
#logo {
  font-size: 2rem;
  cursor: pointer;
  font-family: "Merienda", cursive;
}
ul {
  height: 35vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
}
li {
  margin: 0.1rem;
  font-size: 1.2rem;
  text-align: left;
  padding: 0.6rem;
  transition: ease-in-out all 0.3s;
  border-radius: 0.6rem;
  width: 13vw;
}
img {
  width: 1.4rem;
  height: 1.4rem;
  margin-right: 0.6rem;
}
#thunder-icon {
  width: 6rem;
  height: 6rem;
}
li:hover {
  border: solid 0.05rem white;
  transform: scale(1.02);
}
a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: white;
}
#connect {
  padding: 0.7rem;
  margin: 0.3rem;
  background-color: #e64275;
  outline: none;
  border: none;
  border-radius: 0.8rem;
  color: white;
  font-family: "Montserrat", sans-serif;
  font-size: 1.2rem;
  width: 13vw;
  cursor: pointer;
  box-shadow: 0 0 10px rgb(22, 22, 22);
  transition: ease all 0.3s;
}
#connect:hover {
  transform: scale(1.05);
  background-color: #db376b;
}
@media only screen and (max-width: 800px) {
  .main{
    flex-direction: column;
  }
  nav {
    display: none;
  }
  .nav-below{
      display: flex;
      background-color: rgb(0, 0, 0, 0.7);
      border-bottom-left-radius: 0.9rem;
      border-bottom-right-radius: 0.9rem;
  }
  ul {
    width: 80vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    list-style: none;
    justify-content: space-evenly;
    padding: 0.6rem;
    height: fit-content;
  }
  img {
    width: 1.2rem;
    height: 1.2rem;
    margin-right: 0rem;
  }
  .glass{
      border-bottom: solid 1px #0d0314;
  }
  a{
      justify-content: center;
  }
}
